<template>
  <div class="login-container m-t-40 p-t-40 a-c">
    <h3 class="m-v-40"></h3>
    <div flex="dir:left main:center cross:bottom">
      <vue-input type="password" label="" placeholder="" :value.sync="token" width="300" @onEnter="checkLogin" :autoFocus="true"></vue-input>
    </div>
  </div>
</template>

<script>
import VueInput from './common/form/VueInput'
import Utils from '@/toolkits/Utils'

export default {
  data () {
    return {
      token: ''
    }
  },
  methods: {
    checkLogin () {
      if (this.token) this.loginRemote()
      else this.$toast.show('')
    },
    async loginRemote () {
      const vm = this
      const data = await Utils.login(vm.token)
      if (data) {
        vm.$toast.show(`${data}`)
        setTimeout(() => {
          vm.$parent.$refs.topbar.jumpPath = '/article/edit'
          vm.$router.push('/')
        }, 1000)
      }
    }
  },
  components: {VueInput}
}
</script>

<style lang="scss" scoped>

</style>



